<template>
    <div>
        <img src={{ imgurl }} alt="">
    </div>
</template>

<script>
export default {
    name:'detail',
    data () {
        return {  
            isbuy: '',  
            isshare: '',  
            imgurl: '',  
        }  
    },
    methods: {  
      getParams () {  
        //    
        let buy = this.$route.params.isbuy;
        let share = this.$route.params.isshare;
        let imgurl = this.$route.params.img;
        //   
        this.isbuy = isbuy;
        this.isshare = share;
        this.imgurl = imgurl;
        console.log("buy", this.isbuy); //  test message
        console.log("share", this.isshare); //  test message
        console.log("imgurl", this.imgurl); //  test message
      }  
    },  
    watch: {  
    // ,  
      '$route': 'getParams'  
    }
}
</script>

